import { Component, h, Element, Listen, State } from '@stencil/core/internal';
import tailwind from '../../output.css';
@Component({
  tag: 'rf-display-hotel',
  shadow: true,
})
export class DisplayHotel {
  @Element() element: HTMLElement;

  componentDidLoad() {
    const shadowRoot = this.element.shadowRoot;
    if (shadowRoot) {
      const style = document.createElement('style');
      style.textContent = tailwind;
      shadowRoot.appendChild(style);
    }
  }
  @State() modal= false;

  @Listen('rf_hide', { target: 'body' })
   hide(event: CustomEvent){
    this.modal= event.detail
   }

   @Listen('rf_show', { target: 'body' })
   show(event: CustomEvent){
    this.modal = true
    this.myId = event.detail
   }

   @State() myId: number;

   @Listen('rf_sendId', { target: 'body' })
   getId(event: CustomEvent){
    this.myId = event.detail
    this.modal = true
   }

   @State() popup = false;

   @Listen('rf_show_popup', { target: 'body' })
   showPopup(event: CustomEvent){
    this.popup = event.detail
   }

   @Listen('rf_hide_popup', { target: 'body' })
   hidePopup(event: CustomEvent){
    this.popup = event.detail
   }

  render() {
    return (
      <div class='flex flex-col mx-0 lg:mx-20 xl:mx-60'>
        <rf-navbar></rf-navbar>
        <rf-photo></rf-photo>
        <rf-rooms></rf-rooms>
        {this.modal ? <rf-modal roomId={this.myId}></rf-modal> : null}
        {this.popup ? <rf-popup></rf-popup> : null}
        <rf-facilities></rf-facilities>
      </div>
    );
  }
}
